var _self;
export default {
	data() {
		return {
			maskState: 0, //优惠券面板显示状态
			desc: '', //备注
			payType: 1, //1微信 2支付宝
			couponList: [
				{
					title: '新用户专享优惠券',
					price: 5,
				},
				{
					title: '庆五一发一波优惠券',
					price: 10,
				},
				{
					title: '优惠券优惠券优惠券优惠券',
					price: 15,
				}
			],
			addressData: {
				name: '许小星',
				mobile: '13853989563',
				addressName: '金九大道',
				address: '山东省济南市历城区',
				area: '149号',
				default: false,
			},
			cartList :[]
		}
	},
	onLoad(option){
		_self = this;
		//商品数据
		//let data = JSON.parse(option.data);
		//console.log(data);
		//加载购物车列表
		_self.cartList = uni.getStorageSync('carts');
		//加载默认收货地址
		_self.loadAddress();
	},
	onShow() {

	},
	methods: {
		//加载地址
		async loadAddress(){
			let myAddr = await _self.$apiRequest({
				url:'addresses/1'
			});
			_self.addressData =myAddr.data.data;
			//console.log(_self.addressData);
		},
		//显示优惠券面板
		toggleMask(type){
			let timer = type === 'show' ? 10 : 300;
			let	state = type === 'show' ? 1 : 0;
			this.maskState = 2;
			setTimeout(()=>{
				this.maskState = state;
			}, timer)
		},
		numberChange(data) {
			this.number = data.number;
		},
		changePayType(type){
			this.payType = type;
		},
		submit(){
			//保存订单
			
			_self.$api.msg('支付未启用!!');
			// uni.redirectTo({
			// 	url: '/pages/money/pay'
			// })
		},
		stopPrevent(){}
	}
}
